/* General Styling for My Orders Section */
.my-orders {
    margin: 0px auto 50px;
    width: 100%;
    font-family: 'Arial', sans-serif;
    color: #333;
    padding: 20px;
}

.my-orders h1 {
    text-align: center;
    font-size: 2.5rem;
    color: black;
    margin-bottom: 20px;
}
.status
{
    color:tomato;
}
/* Wrapper for table with overflow */
.table-wrapper {
    width: 100%;
    overflow-x: auto;  /* Allow horizontal scrolling if table is too wide */
}

/* Table Styling */
.orders-table {
    width: 100%;
    border-collapse: collapse;
    table-layout: auto;
    margin-top: 20px;
}

.orders-table th, .orders-table td {
    padding: 12px;
    text-align: left;
    border: 1px solid rgba(155, 151, 151, 0.678);
}

.orders-table th {
    background-color: rgba(41, 39, 39, 0.678);
    color: white;
    font-weight: bold;
}

.orders-table td {
    background-color: #fff;
}

.orders-table tr:nth-child(even) {
    background-color: #f9f9f9;
}

.orders-table .order-img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
}

/* Button Styling */
.track-btn {
    padding: 8px 12px;
    background-color: green;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.track-btn:hover {
    background-color: rgb(24, 190, 24);
}

/* Responsive Design */
@media (max-width: 768px) {
    .orders-table th, .orders-table td {
        padding: 10px;
        font-size: 14px;
    }

    .orders-table .order-img {
        width: 40px;
        height: 40px;
    }

    .track-btn {
        font-size: 12px;
        padding: 6px 10px;
    }
}

@media (max-width: 480px) {
    .orders-table {
        font-size: 12px;
    }

    .orders-table th, .orders-table td {
        padding: 8px;
    }

    .track-btn {
        font-size: 12px;
        padding: 6px 8px;
    }

    .my-orders h1 {
        font-size: 1.8rem;
    }
}
