.explore-menu-navigation {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap; /* Ensure elements wrap on smaller screens */
}

.explore-menu {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: left; /* Center align for smaller screens */
}

.explore-menu-text {
    max-width: 60%;
    text-align: left; /* Center text for better alignment */
}

.explore-menu-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    text-align: center;
    margin: 20px 0px;
    overflow-x: scroll;
    scrollbar-width: none; /* Hide scrollbar for modern browsers */
    -ms-overflow-style: none; /* Hide scrollbar for IE and Edge */
}

.explore-menu-list::-webkit-scrollbar {
    display: none; /* Hide scrollbar for WebKit browsers */
}

.explore-menu-list-item img {
    width: 144px;
    min-width: 144px;
    height: 180px;
    min-height: 180px;
    border-radius: 50px;
    transition: 0.2s;
    cursor: pointer;
}

.fdimg {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.explore-menu hr {
    margin: 10px 0px;
    height: 2px;
    border: none;
    background-color: #ccc;
}

.explore-menu-list-item .active {
    border: 2px solid black;
    padding: 2px;
}

/* Responsive Design */
@media (max-width: 768px) {
    .explore-menu-text {
        padding: 0 10px;
        max-width: 100%; /* Increase text area for smaller screens */
    }

    .explore-menu-list {
        gap: 20px; /* Reduce spacing for smaller screens */
    }

    .explore-menu-list-item img {
        width: 120px; /* Adjust image size */
        height: 150px;
        border-radius: 30px; /* Adjust border radius */
    }
}

@media (max-width: 460px) {
    .explore-menu {
        padding-left: 10px;
    }
    .explore-menu-text {
        max-width: 100%; /* Expand text for extra small screens */
        font-size: 16px; /* Reduce font size */
    }

    .explore-menu-list {
        gap: 10px; /* Further reduce spacing */
    }

    .explore-menu-list-item img {
        width: 100px; /* Further reduce image size */
        height: 130px;
        border-radius: 20px;
    }
}
